*, *:before, *:after {
  box-sizing: inherit; }

/*====================================================*/
body {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 2rem; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  width: 100%; }

h1, h2, h3, h4 {
  margin: 0; }

h1 {
  font-size: 4.5rem; }

h2 {
  font-size: 4rem; }

h3 {
  font-size: 3.8rem; }

h4 {
  font-size: 3rem; }

.centrar-texto {
  text-align: center; }

.producto {
  margin: 0 auto;
  border-radius: .5rem;
  background-color: #ffffff;
  text-align: center;
  -webkit-box-shadow: 0px 0px 11px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 0px 0px 11px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 0px 0px 11px 0px rgba(50, 50, 50, 0.75); }
  .producto p {
    color: #7cbd1e;
    font-weight: bold;
    margin: 0; }
  .producto button {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    background-color: #433520;
    border: none;
    border-radius: .5rem;
    margin: 1.66667rem 0;
    padding: 1rem 7rem;
    cursor: pointer; }
    .producto button:hover {
      background-color: #7cbd1e; }
  .producto img {
    padding: 2rem 0;
    width: 90%; }

.header {
  background-color: #ff7a00;
  color: #ffffff; }
  .header h2 {
    text-transform: uppercase;
    color: #ffffff; }

.header-contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 1200px) {
    .header-contenido {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 1.66667rem; } }

.header-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.66667rem; }
  .header-logo img {
    width: 10rem; }

.buscador {
  margin: 1.66667rem 0; }
  .buscador input {
    border: none;
    padding: 9px 10px 9px 50px;
    outline: none;
    font-size: 1.8rem;
    border-radius: 1.5rem;
    background: #ffffff url("../img/iconSearch3.png") no-repeat 9px center;
    width: 25rem;
    cursor: pointer; }
    @media (min-width: 768px) {
      .buscador input {
        width: 50rem; } }
    @media (min-width: 1200px) {
      .buscador input {
        width: 60rem; } }

.Shop-profile {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem; }

.navPrincipal {
  background-color: #433520; }
  .navPrincipal a {
    color: #ffffff; }

.navPrincipal-contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .navPrincipal-contenido a {
    padding: 1.25rem 0; }
  .navPrincipal-contenido a:hover {
    color: #7cbd1e; }
  @media (min-width: 768px) {
    .navPrincipal-contenido {
      flex-direction: row;
      gap: 5rem; } }

.webp .heroIndex {
  background-image: url(../img/Raspberries&Blueberries.webp); }

.no-webp .heroIndex {
  background-image: url(../img/Raspberries&Blueberries.jpg); }

.heroIndex {
  background-repeat: no-repeat;
  background-position: center center;
  height: 40rem; }

.overlayIndex {
  background-color: rgba(0, 0, 0, 0.5);
  width: auto;
  height: 100%; }

.heroIndex-contenido {
  color: #ffffff;
  padding-top: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase; }

/*============================ HERO SEMILLAS ============================*/
.webp .heroSemillas {
